.App {
  text-align: center;
}
.center {
  margin-left: auto;
  margin-right: auto;

  width: 80%;
  border: none;
  padding: 5px;

  background: rgba(255, 255, 255, 0.4);

}
.noButton {
    all: unset;
    text-decoration: underline;
    color: blue;
}

button {
  border: none;
  background: none;
  margin-top: 10px;
}
button:hover {
  cursor: pointer;
}

p {
  margin-bottom: 5px;
  line-height: 1.4em;
}
input[type=text] {
  width: 80%;
  padding: 12px 20px;
  margin: 8px 4px;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.5);
}
input.toFrom {
  width: 20%;
  padding: 12px 20px;
  margin: 8px 4px;
  box-sizing: border-box;
  background: rgba(255, 255, 255, 0.5);
}
ol
    {
       display: inline-block;
       margin-left: auto;
       margin-right: auto;
    }
